<template>
  <div></div>
</template>

<script setup>
// 導入pixi.js
import * as PIXI from 'pixi.js'

// 創建應用
const app = new PIXI.Application({
  width: window.innerWidth,
  height: window.innerHeight,
  backgroundColor: 'white',
  resolution: window.devicePixelRatio || 1, // 設置解析度
  antialias: true,  //抗鋸齒
})

// 設定目標幀數為 60 FPS
app.ticker.maxFPS = 60
// app.ticker.minFPS = 30  // 最低幀數

// 將應用畫布添加到DOM中
document.body.appendChild(app.view)

// 創建容器
const container = new PIXI.Container()
app.stage.addChild(container)

// 創建一個紋理
const baseTexture = PIXI.BaseTexture.from("./textures/googleDragon.png")

const pinoTexture = new PIXI.Texture(baseTexture, new PIXI.Rectangle(10, 0, 80, 50))
const pino = new PIXI.Sprite(pinoTexture)
pino.position.set(10, app.screen.height - 85)
container.addChild(pino)

// 恐龍跑步精靈
const runTexture = []
for (let i = 0; i < 2; i++) {
  runTexture.push(
    new PIXI.Texture(baseTexture, new PIXI.Rectangle(935 + (i * 45), 0, 45, 50))
  )
}

// 恐龍跑步動畫
const runAnimation = new PIXI.AnimatedSprite(runTexture)
runAnimation.animationSpeed = 0.1 // 動畫速度
runAnimation.visible = false // 設置為可見
runAnimation.position.set(10, app.screen.height - 85)
// runAnimation.play() // 播放動畫
container.addChild(runAnimation)


// 恐龍蹲下精靈
const squatTexture = []
for (let i = 0; i < 2; i++) {
  squatTexture.push(
    new PIXI.Texture(baseTexture, new PIXI.Rectangle(1110 + (i * 60), 20, 60, 30))
  )
}


// 飛鳥精靈
const flyBirdTexture = []
for (let i = 0; i < 2; i++) {
  flyBirdTexture.push(
    new PIXI.Texture(baseTexture, new PIXI.Rectangle(135 + (i * 45), 0, 45, 30))
  )
}

// 飛鳥動畫
const flyBirdAnimation = new PIXI.AnimatedSprite(flyBirdTexture)
flyBirdAnimation.animationSpeed = 0.1 // 動畫速度
flyBirdAnimation.visible = false // 設置為可見
flyBirdAnimation.position.set(getFlyBirdX() , app.screen.height - 100)
// flyBirdAnimation.play() // 播放動畫
container.addChild(flyBirdAnimation)

function getFlyBirdX() {
  let begin = app.screen.width + 400; // 飛鳥的起始位置
  let end = app.screen.width + 600; // 飛鳥的結束位置
  // 隨機生成飛鳥的X位置
  return Math.floor(Math.random() * (begin - end + 1)) + end;
}


// 地面精靈
const groundTexture = new PIXI.Texture(baseTexture, new PIXI.Rectangle(10, 50, 1200, 20))
const ground = new PIXI.TilingSprite(groundTexture) // 使用TilingSprite來平鋪地面紋理
ground.width = app.screen.width
ground.height = 20
ground.position.set(0, app.screen.height - 50)
container.addChild(ground)

// 仙人掌
const cactusTexture = new PIXI.Texture(baseTexture, new PIXI.Rectangle(356, 0, 25, 55))
const cactus = new PIXI.Sprite(cactusTexture)
cactus.position.set(app.screen.width / 2, app.screen.height - 85)
container.addChild(cactus)


const textStartGame = new PIXI.Text('點擊我開始遊戲', {
  fontSize: 30,
  align: 'center',
})

textStartGame.anchor.set(0.5)
textStartGame.position.set(app.screen.width / 2, app.screen.height / 2)
container.addChild(textStartGame)

textStartGame.interactive = true
textStartGame.on('pointerdown', () => {
  console.log("@@@點擊開始遊戲")
  playGame()
})

let isPlayGame = false // 遊戲是否開始
let speed = 7 // 遊戲速度
let isJump = false // 是否跳躍
let up = true
function playGame() {
  textStartGame.visible = false // 點擊後隱藏文字
  textTip.visible = true // 顯示分數提示
  isPlayGame = true // 遊戲開始
  pino.visible = false // 顯示恐龍
  runAnimation.visible = true // 顯示跑步動畫
  runAnimation.play() // 播放跑步動畫

}

function gameOver() {
  isPlayGame = false // 遊戲結束
  textTip.style = new PIXI.TextStyle({
    fontSize: 30,
    align: 'center',
    lineHeight: 20,  // 設定行高，讓多行文字更好看
  })
  textTip.text = `遊戲結束，最終分數為： ${score}
  \n點擊我重新開始`// 更新分數提示
  textTip.interactive = true // 重新開始遊戲
  textTip.on('pointerdown', () => {
    location.reload()
  })
}

let score = 0 // 分數

const textTip = new PIXI.Text('當前分數為： ' + score, {
  fontSize: 30,
  align: 'center',
})
textTip.anchor.set(0.5)
textTip.position.set(app.screen.width / 2, app.screen.height / 2)
textTip.visible = false // 初始隱藏
container.addChild(textTip)

// 
app.ticker.add((deltaTime) => {
  // console.log("@@@每幀執行", deltaTime)
  if (isPlayGame) {
    const moveSpeed = speed * deltaTime * 1
    ground.tilePosition.x -= moveSpeed
    cactus.x -= moveSpeed
    if (cactus.x <= 0) {
      cactus.x = app.screen.width
      score += 1
    }
    if (flyBirdAnimation.visible) {
      flyBirdAnimation.x -= moveSpeed
      if (flyBirdAnimation.x <= 0) {
        flyBirdAnimation.x = getFlyBirdX()
        score += 1
      }
    }
    textTip.text = '當前分數為： ' + score // 更新分數提示
    if (score == 5) {
      flyBirdAnimation.visible = true // 顯示飛鳥動畫
      flyBirdAnimation.play() // 播放飛鳥動畫
    } else if (score == 10) {
      speed = 10 // 提高速度
    } else if (score == 15) {
      speed = 15 // 再提高速度
    }

    if (isJump) {
      const jumpSpeed = 10 * deltaTime
      if (up) {
        runAnimation.y -= jumpSpeed
      } else {
        runAnimation.y += jumpSpeed
      }
      if (runAnimation.y <= ground.y - 150) {
        up = false // 到達最高點後開始下降
      }
      if (runAnimation.y > ground.y - 40) {
        isJump = false // 回到地面後停止跳躍
      }
    }

    // 碰撞檢測
    if (checkCollision(runAnimation, cactus)) {
      console.log('@@@撞到仙人掌！')
      gameOver()
    }

    if (checkCollision(runAnimation, flyBirdAnimation)) {
      console.log('@@@撞到飛鳥！')
      gameOver()
    }
  }
})

let y = runAnimation.y // 跑步動畫的初始Y位置
window.addEventListener('keydown', (e) => {
  if (e.code == 'ArrowUp') {
    e.preventDefault() // 阻止默認行為
    isJump = true // 開始跳躍
    up = true // 設置為向上跳躍
  } if (e.code == 'ArrowDown') {
    e.preventDefault() // 阻止默認行為
    runAnimation.textures = squatTexture// 切換到蹲下動畫
    runAnimation.y = y + 20
    runAnimation.play() // 播放蹲下動畫
  }
})

window.addEventListener('keyup', (e) => {
  if (e.code == 'ArrowDown') {
    e.preventDefault() // 阻止默認行為
    runAnimation.textures = runTexture // 切換回跑步動畫
    runAnimation.y = y
    runAnimation.play() // 播放跑步動畫
  }
})

// 檢查兩個精靈是否碰撞
// 更精確的碰撞檢測（縮小檢測區域）
function checkCollision(obj1, obj2) {
  const bounds1 = obj1.getBounds()
  const bounds2 = obj2.getBounds()

  // 添加邊距，讓遊戲更友善
  const padding = 4

  return bounds1.x + padding < bounds2.x + bounds2.width - padding &&
    bounds1.x + bounds1.width - padding > bounds2.x + padding &&
    bounds1.y + padding < bounds2.y + bounds2.height - padding &&
    bounds1.y + bounds1.height - padding > bounds2.y + padding
}

// 添加文字
const textCopyRight = new PIXI.Text(
  `    點擊鍵盤"上"為跳躍，鍵盤"下"為蹲下
  本遊戲為學習使用，若有侵權請告知，謝謝！
       ivesshe@gmail.com
          By PIXI.js`,
  {
    fontFamily: "Ariel",
    fontSize: 20,//+Math.floor(app.screen.width*0.1),
    fill: 0xffffff,
    align: "center",//
    dropShadow: true,
    dropShadowColor: "#000000",
    dropShadowBlur: 4,
    dropShadowAngle: Math.PI / 2,
    dropShadowDistance: 2,

  }
)
textCopyRight.x = app.screen.width / 2;
textCopyRight.y = app.screen.height / 2 - 100;
textCopyRight.anchor.set(0.5);
container.addChild(textCopyRight)



// // 監聽點擊事件，根據位置創建震波濾鏡
// app.view.addEventListener("click",(e)=>{
//   shockwaveFilter1.center = [e.clientX,e.clientY]
//   shockwaveFilter1.time = 0
//   console.log("@@@點擊了!!",e.clientX,e.clientY)
// })


</script>

